<template>
  <div class="container">
    <div class="form">
      <el-form label-width="120px" :inline="true" :model="lifeTableSearch" ref="lifeTableSearchForm">
        <!--<el-form-item label="配费模式:" prop="configCostModel">-->
          <!--<el-select placeholder="请选择配费模式" v-model="lifeTableSearch.configCostModel">-->
            <!--<el-option-->
              <!--v-for="configCostModel in configCostModelList"-->
              <!--:key="configCostModel.configCostModelCode"-->
              <!--:label="configCostModel.configCostModelName"-->
              <!--:value="configCostModel.configCostModelCode">-->
            <!--</el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <el-form-item label="机构代码:" prop="orgCode">
          <el-select placeholder="请选择机构" v-model="lifeTableSearch.orgCode">
            <el-option
              v-for="org in orgList"
              :key="org.orgCode"
              :label="org.orgName"
              :value="org.orgCode">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="报价策略渠道:" prop="channel">
          <el-select placeholder="请选择渠道" v-model="lifeTableSearch.channel">
            <el-option
              v-for="channel in channelList"
              :key="channel.channelCode"
              :label="channel.channelName"
              :value="channel.channelCode">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="版本号:" prop="version">
          <el-input v-model="lifeTableSearch.version">
          </el-input>
        </el-form-item>
        <el-form-item label="操作员:" prop="approval">
          <el-input v-model="lifeTableSearch.approval">
          </el-input>
        </el-form-item>
        <el-form-item label="申报状态:" prop="status">
          <el-checkbox-group v-model="lifeTableSearch.status">
            <el-checkbox
              v-for="status in statusList"
              :key="status.statusCode"
              :label="status.statusCode"
              name="status">{{status.statusName}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
    </div>
    <div class="option">
      <el-button type="primary" size="small" plain  @click="resetForm">重置</el-button>
      <el-button type="primary" size="small" plain>查询</el-button>
    </div>
    <div class="data">
      <div class="table">
        <el-table style="width: 100%"  :data="currentData"  stripe :header-cell-style="tableHeader">
          <el-table-column
              v-for="column in columns"
              header-align="center"
              :key="column.prop"
              :prop="column.prop"
              :min-width="column.width"
              :label="column.label"
              :align="column.align">
          </el-table-column>
          <el-table-column header-align="center"  align="center" label="操作" min-width="100">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="detail(scope.row.version)">查看</el-button>
              <!--<span v-if="scope.row.status==='待申报'">|</span>
              <el-button v-if="scope.row.status==='待申报'" type="text" size="small" @click="modify(scope.row.version)">修改</el-button>-->
            </template>
          </el-table-column>
        </el-table>
      </div>
      <communal-pagination
        @search="search"
        :currentPage="currentPage"
        :pageSize="pageSize"
        :total="data.length">
      </communal-pagination>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const CommunalPagination = () => import('@/components/CommunalPagination')
export default {
  name: 'IndividualQuery',
  components: {CommunalPagination},
  data () {
    return {
      lifeTableSearch: {
        configCostModel: '',
        orgCode: '',
        channel: '',
        version: '',
        approval: '',
        status: []
      },
      configCostModelList: [
        {configCostModelCode: '001', configCostModelName: '个车模式'},
        {configCostModelCode: '002', configCostModelName: '车队模式'}
      ],
      orgList: [
        {orgCode: '000000', orgName: '北京分公司'},
        {orgCode: '000001', orgName: '天津分公司'},
        {orgCode: '000002', orgName: '上海分公司'}
      ],
      channelList: [
        {channelCode: '001', channelName: '保险公司车险'},
        {channelCode: '002', channelName: '银行邮政代理'},
        {channelCode: '003', channelName: '电话车险'},
        {channelCode: '004', channelName: '网销车险'},
        {channelCode: '005', channelName: '4S店保险中心车险'},
        {channelCode: '006', channelName: '维修店专业车险'}
      ],
      statusList: [
        {statusCode: '1', statusName: '待申报'},
        {statusCode: '2', statusName: '待审批'},
        {statusCode: '3', statusName: '已审批'},
        {statusCode: '4', statusName: '待修改'},
        {statusCode: '5', statusName: '需要重新申报'}
      ],
      columns: [
        // { prop: 'configCostModel', width: 150, label: '配费模式', align: 'center' },
        { prop: 'org', width: 200, label: '编制机构', align: 'center' },
        { prop: 'channel', width: 250, label: '渠道', align: 'center' },
        { prop: 'version', width: 250, label: '版本号', align: 'center' },
        { prop: 'declarer', width: 150, label: '申报人', align: 'center' },
        { prop: 'createTime', width: 200, label: '申报时间', align: 'center' },
        { prop: 'status', width: 100, label: '申报状态', align: 'center' },
        { prop: 'latestApproval', width: 120, label: '最新审批人', align: 'center' }
      ],
      data: [
        {
          version: '0200120180701001',
          org: '北京分公司',
          channel: '网销车险',
          configCostModel: '个车模式',
          declarer: '刘芳',
          createTime: '2018/07/01',
          status: '待审批',
          latestApproval: '张三'
        },
        {
          version: '0200220180501002',
          org: '天津分公司',
          channel: '4S店保险中心车险',
          configCostModel: '车队模式',
          declarer: '张强',
          createTime: '2018/05/01',
          status: '待申报',
          latestApproval: '张三'
        },
        {
          version: '0200320180301003',
          org: '上海分公司',
          channel: '维修店专业车险',
          configCostModel: '个车模式',
          declarer: '李菲',
          createTime: '2018/03/01',
          status: '待申报',
          latestApproval: '张三'
        },
        {
          version: '0200120170701004',
          org: '北京分公司',
          channel: '电话车险',
          configCostModel: '个车模式',
          declarer: '刘芳',
          createTime: '2017/07/01',
          status: '待修改',
          latestApproval: '张三'
        },
        {
          version: '0200220170501005',
          org: '天津分公司',
          channel: '银行邮政代理',
          configCostModel: '车队模式',
          declarer: '张强',
          createTime: '2017/05/01',
          status: '需要重新申报',
          latestApproval: '张三'
        },
        {
          version: '0200320170301006',
          org: '上海分公司',
          channel: '网销车险',
          configCostModel: '个车模式',
          declarer: '李菲',
          createTime: '2017/03/01',
          status: '已审批',
          latestApproval: '张三'
        },
        {
          version: '0200120160701007',
          org: '北京分公司',
          channel: '网销车险',
          configCostModel: '个车模式',
          declarer: '刘芳',
          createTime: '2016/07/01',
          status: '待审批',
          latestApproval: '张三'
        },
        {
          version: '0200220160501008',
          org: '天津分公司',
          channel: '保险公司车险',
          configCostModel: '车队模式',
          declarer: '张强',
          createTime: '2016/05/01',
          status: '待申报',
          latestApproval: '张三'
        },
        {
          version: '0200320160301009',
          org: '上海分公司',
          channel: '网销车险',
          configCostModel: '个车模式',
          declarer: '李菲',
          createTime: '2016/03/01',
          status: '已审批',
          latestApproval: '张三'
        }
      ],
      currentPage: 1,
      pageSize: 2
    }
  },
  computed: {
    currentData () {
      const pageData = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end <= this.data.length ? this.currentPage * this.pageSize : this.data.length
      for (let i = start; i < end; ++i) {
        pageData.push(this.data[i])
      }
      return pageData
    },
    ...mapState({tableHeader: state => state.app.tableHeader})
  },
  methods: {
    search (pageNumber, pageSize) {
      this.currentPage = pageNumber
      this.pageSize = pageSize
      console.log(pageNumber)
    },
    modify (version) {
    },
    detail (version) {
      this.$router.push({path: '/admin/individual/query/detail'})
    },
    resetForm () {
      this.$refs['lifeTableSearchForm'].resetFields()
    }
  }
}
</script>

<style scoped>

</style>



